.Keypad button:focus{box-shadow:0 0 0 0.1rem rgba(255,255,255,0.3)}.Screen.alert{padding:0.5rem;min-height:80px;margin-bottom:0.25rem;position:relative}.Screen.alert .buffer-amount{font-size:25px;line-height:25px;text-align:right}.Screen.alert .Transaction{position:absolute;top:-1px;left:-1px;right:-1px;padding:1px 5px;font-family:'Roboto Mono', monospace;font-size:smaller}.Screen.alert .Transaction+div{margin-top:15px}.Keys{padding-bottom:0.25rem}.Keys>.no-gutters{margin-left:-0.25rem;margin-right:-0.25rem}.Keys button{width:100%;height:100%;font-family:'Nato Sans', sans-serif;font-size:26px}.Keys button.action{font-size:20px;height:2.75rem;background-image:none !important;overflow:hidden}.Keys button.action:hover[disabled]{border-color:#333}.Keys .btn-group{border:0;background-color:transparent;width:100%}.Keys .btn-group .dropdown-toggle{font-family:'Nato Sans', sans-serif;font-size:25px;background-image:none !important;width:auto}.Keys .btn-group .dropdown-menu{width:100%;border:1px solid #1a1919}.Keys .btn-group .dropdown-item{font-size:20px}.Keys .btn-group-vertical{height:100%}.Keys .btn-group-vertical button{height:auto}.Keys .btn-group-vertical .btn-sm{font-size:14px}

